"use client";

import React, { useRef, useState, useEffect } from "react";
import { filters } from "./filters";
import { borders } from "./borders";

export default function PhotoBoothPage() {
  const videoRef = useRef<HTMLVideoElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [hasPhoto, setHasPhoto] = useState(false);
  const [photoData, setPhotoData] = useState<string | null>(null);
  const [countdown, setCountdown] = useState<number | null>(null);
  const [flash, setFlash] = useState(false);
  const [isReady, setIsReady] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [selectedFilter, setSelectedFilter] = useState(filters[0]);
  const [selectedBorder, setSelectedBorder] = useState(borders[0]);

  useEffect(() => {
    let mounted = true;

    async function initializeCamera() {
      try {
        setIsLoading(true);
        setError(null);

        const stream = await navigator.mediaDevices.getUserMedia({
          video: {
            width: { ideal: 1280 },
            height: { ideal: 720 },
            facingMode: "user"
          }
        });

        if (!mounted) {
          stream.getTracks().forEach(track => track.stop());
          return;
        }

        if (videoRef.current) {
          videoRef.current.srcObject = stream;
          await videoRef.current.play();
          setIsReady(true);
        }
      } catch (err: any) {
        if (mounted) {
          if (err.name === 'NotAllowedError') {
            setError("Camera access denied. Please allow camera access and refresh the page.");
          } else if (err.name === 'NotFoundError') {
            setError("No camera found. Please connect a camera and refresh the page.");
          } else {
            setError("Failed to initialize camera. Please try again.");
          }
        }
      } finally {
        if (mounted) {
          setIsLoading(false);
        }
      }
    }

    initializeCamera();

    return () => {
      mounted = false;
      const stream = videoRef.current?.srcObject as MediaStream;
      stream?.getTracks().forEach(track => track.stop());
    };
  }, []);

  useEffect(() => {
    let timer: NodeJS.Timeout;
    if (countdown !== null && countdown > 0) {
      timer = setTimeout(() => setCountdown(countdown - 1), 1000);
    } else if (countdown === 0) {
      takePhoto();
      setCountdown(null);
      setFlash(true);
      setTimeout(() => setFlash(false), 200);
    }
    return () => clearTimeout(timer);
  }, [countdown]);

  const startCountdown = () => {
    setCountdown(3);
  };

  const takePhoto = () => {
    if (canvasRef.current && videoRef.current) {
      const width = videoRef.current.videoWidth;
      const height = videoRef.current.videoHeight;
      canvasRef.current.width = width;
      canvasRef.current.height = height;
      const ctx = canvasRef.current.getContext("2d");
      if (ctx) {
        // Apply selected filter
        ctx.filter = selectedFilter.filter;
        ctx.drawImage(videoRef.current, 0, 0, width, height);

        // Apply selected border if any
        if (selectedBorder.overlay) {
          const borderImg = new Image();
          borderImg.onload = () => {
            ctx.drawImage(borderImg, 0, 0, width, height);
            const data = canvasRef.current!.toDataURL("image/png");
            setPhotoData(data);
          };
          borderImg.src = selectedBorder.overlay;
        } else {
          const data = canvasRef.current.toDataURL("image/png");
          setPhotoData(data);
        }
        setHasPhoto(true);
      }
    }
  };

  const closePhoto = () => {
    setHasPhoto(false);
    setPhotoData(null);
  };

  const retryCamera = () => {
    window.location.reload();
  };

  return (
    <div className="min-h-screen bg-[radial-gradient(circle_at_center,_var(--tw-gradient-stops))] from-purple-950 via-gray-900 to-black text-white overflow-hidden">
      {/* Decorative Background Elements */}
      <div className="fixed inset-0 pointer-events-none">
        <div className="absolute top-0 left-1/4 w-48 md:w-96 h-48 md:h-96 bg-purple-500/20 rounded-full blur-3xl animate-pulse"></div>
        <div className="absolute bottom-0 right-1/4 w-48 md:w-96 h-48 md:h-96 bg-blue-500/20 rounded-full blur-3xl animate-pulse delay-1000"></div>
        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-full h-full bg-[radial-gradient(circle_at_center,transparent_0%,rgba(0,0,0,0.8)_100%)]"></div>
      </div>

      <div className="container mx-auto px-4 py-4 md:py-8 min-h-screen flex flex-col items-center justify-center relative">
        {/* Header */}
        <div className="text-center mb-6 md:mb-12 relative">
          <h1 className="text-3xl sm:text-4xl md:text-6xl lg:text-7xl font-black tracking-tight mb-2 md:mb-4">
            <span className="bg-gradient-to-r from-white via-purple-200 to-purple-400 bg-clip-text text-transparent">
              Digital Photo Booth
            </span>
          </h1>
          <p className="text-base md:text-xl text-purple-300 font-medium">
            Strike a pose and create memories ✨
          </p>
        </div>

        {/* Main Photo Booth Interface */}
        <div className="w-full max-w-4xl perspective-1000">
          <div className="bg-gradient-to-b from-zinc-800 to-zinc-900 rounded-2xl md:rounded-3xl p-4 md:p-8 shadow-[0_0_50px_rgba(168,85,247,0.15)] relative transform transition-all duration-500 hover:shadow-[0_0_80px_rgba(168,85,247,0.25)] animate-border-glow">
            {/* Camera Top Panel */}
            <div className="absolute -top-4 md:-top-6 left-1/2 transform -translate-x-1/2 w-40 md:w-56 h-8 md:h-12 bg-zinc-700 rounded-xl md:rounded-2xl flex items-center justify-center gap-4 md:gap-6 px-4 md:px-6 border-t-2 border-zinc-600">
              <div className="w-3 md:w-4 h-3 md:h-4 rounded-full bg-red-500 animate-pulse"></div>
              <div className="w-3 md:w-4 h-3 md:h-4 rounded-full bg-yellow-500 animate-pulse delay-100"></div>
              <div className="w-3 md:w-4 h-3 md:h-4 rounded-full bg-green-500 animate-pulse delay-200"></div>
            </div>

            {/* Camera Viewport */}
            <div className="relative aspect-[4/3] md:aspect-video rounded-xl md:rounded-2xl overflow-hidden border-[12px] md:border-[16px] border-zinc-900 shadow-inner bg-black">
              {/* Loading State */}
              {isLoading && (
                <div className="absolute inset-0 flex items-center justify-center bg-black/90 z-20">
                  <div className="flex flex-col items-center">
                    <div className="w-16 md:w-20 h-16 md:h-20 border-4 border-purple-500 border-t-transparent rounded-full animate-spin"></div>
                    <p className="mt-4 md:mt-6 text-base md:text-lg text-purple-300">Initializing camera...</p>
                  </div>
                </div>
              )}

              {/* Error State */}
              {error && (
                <div className="absolute inset-0 flex items-center justify-center bg-black/90 z-20 p-4 md:p-8">
                  <div className="text-center max-w-md">
                    <div className="w-16 md:w-20 h-16 md:h-20 mx-auto mb-4 md:mb-6 text-red-500">
                      <svg className="w-full h-full" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
                      </svg>
                    </div>
                    <p className="text-base md:text-xl text-red-400 mb-4 md:mb-6">{error}</p>
                    <button
                      onClick={retryCamera}
                      className="px-6 md:px-8 py-2 md:py-3 bg-purple-600 rounded-lg md:rounded-xl hover:bg-purple-700 transition-colors transform hover:scale-105 duration-300 text-sm md:text-base"
                    >
                      Retry Camera Access
                    </button>
                  </div>
                </div>
              )}

              {/* Camera View */}
              <video
                ref={videoRef}
                className={`w-full h-full object-cover transition-all duration-700 ${
                  hasPhoto ? 'opacity-0 scale-110' : 'opacity-100 scale-100'
                }`}
                style={{ filter: selectedFilter.filter }}
                playsInline
                muted
              />
              <canvas
                ref={canvasRef}
                className={`absolute inset-0 w-full h-full object-cover transition-all duration-700 ${
                  hasPhoto ? 'opacity-100 scale-100' : 'opacity-0 scale-90'
                }`}
              />

              {/* Border Overlay */}
              {selectedBorder.overlay && !hasPhoto && (
                <div 
                  className="absolute inset-0 pointer-events-none z-10"
                  style={{
                    backgroundImage: `url(${selectedBorder.overlay})`,
                    backgroundSize: '100% 100%'
                  }}
                />
              )}

              {/* Flash Effect */}
              {flash && (
                <div className="absolute inset-0 bg-white animate-flash z-10"></div>
              )}

              {/* Viewfinder Overlay */}
              <div className="absolute inset-0 pointer-events-none z-10">
                <div className="absolute top-4 md:top-6 left-4 md:left-6 w-12 md:w-20 h-12 md:h-20 border-t-2 border-l-2 border-white opacity-30"></div>
                <div className="absolute top-4 md:top-6 right-4 md:right-6 w-12 md:w-20 h-12 md:h-20 border-t-2 border-r-2 border-white opacity-30"></div>
                <div className="absolute bottom-4 md:bottom-6 left-4 md:left-6 w-12 md:w-20 h-12 md:h-20 border-b-2 border-l-2 border-white opacity-30"></div>
                <div className="absolute bottom-4 md:bottom-6 right-4 md:right-6 w-12 md:w-20 h-12 md:h-20 border-b-2 border-r-2 border-white opacity-30"></div>
                <div className="absolute inset-0 border-2 border-white/10"></div>
              </div>

              {/* Countdown Overlay */}
              {countdown !== null && (
                <div className="absolute inset-0 flex items-center justify-center bg-black/50 backdrop-blur-sm z-10">
                  <div className="text-7xl md:text-[150px] font-black text-white animate-bounce drop-shadow-lg">
                    {countdown}
                  </div>
                </div>
              )}
            </div>

            {/* Filter and Border Selection */}
            {!hasPhoto && (
              <div className="mt-6 space-y-6">
                {/* Filter Selection */}
                <div>
                  <h3 className="text-center text-lg md:text-xl mb-4 text-purple-300">Choose a Filter</h3>
                  <div className="flex flex-wrap justify-center gap-3">
                    {filters.map((filter) => (
                      <button
                        key={filter.id}
                        onClick={() => setSelectedFilter(filter)}
                        className={`px-4 py-2 rounded-full text-sm md:text-base transition-all duration-300 ${
                          selectedFilter.id === filter.id
                            ? 'bg-purple-600 text-white'
                            : 'bg-zinc-800 text-gray-300 hover:bg-zinc-700'
                        }`}
                      >
                        {filter.name}
                      </button>
                    ))}
                  </div>
                </div>

                {/* Border Selection */}
                <div>
                  <h3 className="text-center text-lg md:text-xl mb-4 text-purple-300">Choose a Border</h3>
                  <div className="flex flex-wrap justify-center gap-3">
                    {borders.map((border) => (
                      <button
                        key={border.id}
                        onClick={() => setSelectedBorder(border)}
                        className={`px-4 py-2 rounded-full text-sm md:text-base transition-all duration-300 ${
                          selectedBorder.id === border.id
                            ? 'bg-purple-600 text-white'
                            : 'bg-zinc-800 text-gray-300 hover:bg-zinc-700'
                        }`}
                      >
                        {border.name}
                      </button>
                    ))}
                  </div>
                </div>
              </div>
            )}

            {/* Controls */}
            <div className="mt-6 md:mt-8 flex flex-col md:flex-row items-center justify-center space-y-4 md:space-y-0 md:space-x-6">
              {!hasPhoto ? (
                <button
                  onClick={startCountdown}
                  disabled={countdown !== null || !isReady || !!error}
                  className="w-full md:w-auto px-8 md:px-10 py-4 md:py-5 bg-gradient-to-r from-purple-600 to-purple-800 rounded-full text-white font-bold text-lg md:text-xl transform hover:scale-105 active:scale-95 transition-all duration-300 disabled:opacity-50 disabled:cursor-not-allowed disabled:hover:scale-100 shadow-lg hover:shadow-purple-500/25"
                >
                  <span className="relative z-10 flex items-center justify-center space-x-3">
                    <span>{isReady ? 'Take Photo' : 'Initializing...'}</span>
                    <svg className="w-6 md:w-7 h-6 md:h-7 animate-[spin_3s_linear_infinite]" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <circle cx="12" cy="12" r="10" strokeWidth="2"/>
                      <circle cx="12" cy="12" r="3" strokeWidth="2"/>
                    </svg>
                  </span>
                </button>
              ) : (
                <div className="flex flex-col md:flex-row w-full md:w-auto space-y-4 md:space-y-0 md:space-x-4">
                  <a
                    href={photoData ?? "#"}
                    download="photo-booth-capture.png"
                    className="w-full md:w-auto px-8 md:px-10 py-4 md:py-5 bg-gradient-to-r from-emerald-500 to-teal-600 rounded-full text-white font-bold text-lg md:text-xl transform hover:scale-105 active:scale-95 transition-all duration-300 flex items-center justify-center space-x-3 shadow-lg hover:shadow-emerald-500/25"
                  >
                    <span>Download</span>
                    <svg className="w-6 md:w-7 h-6 md:h-7" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-4l-4 4m0 0l-4-4m4 4V4"/>
                    </svg>
                  </a>
                  <button
                    onClick={closePhoto}
                    className="w-full md:w-auto px-8 md:px-10 py-4 md:py-5 bg-zinc-700 hover:bg-zinc-600 rounded-full text-white font-bold text-lg md:text-xl transform hover:scale-105 active:scale-95 transition-all duration-300 shadow-lg"
                  >
                    Retake
                  </button>
                </div>
              )}
            </div>
          </div>

          {/* Status Indicator */}
          <div className="mt-4 md:mt-6 flex items-center justify-center space-x-3 text-sm md:text-base text-purple-300">
            <div className={`w-2.5 md:w-3 h-2.5 md:h-3 rounded-full ${isReady ? 'bg-green-500' : 'bg-yellow-500'} animate-pulse`}></div>
            <span>{isReady ? 'Camera Ready' : 'Initializing Camera...'}</span>
          </div>
        </div>
      </div>
    </div>
  );
}
